@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap);
body {
    margin: 0;
    font-family: 'Noto Sans KR', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: 'Noto Sans KR', sans-serif;
  }
  ul,li{
    padding:0px;
    margin:0px;
  }
  li{
    list-style:none;
  }
  a{
    text-decoration: none;
    color:black;
  }
.header{
    width: 100%;
    position: fixed;
    /* background-color: rgb(242, 170, 170); */
    background-color: white;
    top:0;
    z-index: 110;
    background-color: #59aaeb;
}
.top{
    width:90%;
    margin:0 auto;
}
.top .logo a{
    font-size:30px;
    font-weight: 600;
    display: inline-block;
}
.top .logo{
    display: inline-block;
}
.top .navigation{
    float:right;
}
.navigation > .pc_navigation ul li{
    float:left;
    font-weight: 600;
    margin-top: 2px;
    padding:15px 0px;
    transition: transform .3s;
}
.navigation > .pc_navigation ul li:hover{
    transform: scale(1.3);
}

.navigation > .pc_navigation ul li a{
    color: black;
    padding:15px;
}

@media (max-width: 768px) {
    .navigation > .pc_navigation{
        display: none;
    }
}
.footer{
    width:100%;
    height:50px;
    margin-top:50px;
    border-top:1px solid #e3e3e3;
}
.pc_footer{
    width:80%;
    margin:0 auto;
    padding:30px 0px 50px 0px;
}
.copyright{
    float:left;
}
.sitemap{
    float:left;
    margin-left:50px;
}
.sitemap ul li{
    float: left;
    padding-left:10px;
}
.SNS{
    float:right;
    margin-right:50px;
}
.SNS ul li{
    padding-left:20px;
    float:left;
}
.mob_menu{
    display: none;
    /* background-color: rgb(242, 170, 170); */
    background-color:#59aaeb;
    width:100%;
    height:50px;
    position: fixed;
    margin-top: 50px;
    bottom: 0;
box-shadow: 3px 3px 3px 0px rgba(166,228,255,1);
}
.mob_menu ul{
    display: flex;
}
.mob_menu ul li{
    float:left;
    /* color:black; */
    font-weight: 600;
    flex:1 1;
    text-align: center;
}
.mob_menu ul li a{
    color:black;
    display: inline-block;
    padding: 17px 0px;
}

@media (max-width: 768px) {
    .footer{
        margin-top: 0px;
    }
    .pc_footer{
        display: none;
    }
    .mob_menu{
        display: block;
    }
    .mobile_navi_click{
        color:red !important;
    }
}

.poster1{
    background-color: rgb(185,234,255);
    width:100%;
    min-height: 250px;
}
.poster1_on{
    transition:7s;
    opacity: 1;
}
.poster_img{
    min-height: 250px;
    background-color: rgb(88,204,255);
}
/* 포스터 2 animation */

.poster_img_on{
    transition: 10s;
    opacity: 1;
}

.none{
    transition: 2.5s;
    zoom: 0.8;
    font-weight: 600;
    opacity: 0;
}
.animate_wrap{
    margin: 0 auto;
    width: 80%;
    text-align: center;
    position:absolute;
    bottom: 55%;
    left: 10%;
}
/* 포스터 1 텍스트 animation */
.animate_text{
    transition:3.5s;
    color:rgb(189,91,97);
    font-size:46px;
    margin:0;
}
.animate_text1{
    transition: 4s;
    font-weight: 600;
    opacity: 1;
    color:rgb(189,91,97);
    font-size:52px;
    margin:0;
}

@media (max-width: 1400px) {
    .animate_wrap{
        bottom: 60%;
    }
}

@media (max-width: 1260px) {
    .animate_wrap{
        bottom: 63%;
    }
}

@media (max-width: 1058px) {
    .animate_wrap{
        bottom: 69%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:36px;
    }
    .animate_text1{
        font-size:42px;
    }
}

@media (max-width: 798px) {
    .animate_wrap{
        bottom: 76%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:26px;
    }
    .animate_text1{
        font-size:32px;
    }
}

@media (max-width: 580px) {
    .animate_wrap{
        bottom: 78%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:22px;
    }
    .animate_text1{
        font-size:28px;
    }
}

@media (max-width: 480px) {
    .animate_wrap{
        bottom: 77.5%;
    }
    .animate_text{
        /* transition: 2.5s; */
        /* opacity: 1; */
        font-size:17px;
    }
    .animate_text1{
        font-size:23px;
    }
}
.signup_wrap{
    margin: 100px 0px 50px 0px;
    min-height: 450px;
}
.signup_form{
    height: 100%;
    margin:0 auto;
    border:1px solid rgb(90, 130, 190);
    border-radius: 25px;
    width: 35%;
    margin: 0 auto;
    padding: 2% 5%;
}
.signup_form{
    margin:0 auto;
}
.signup_form > h2{
    text-align: center;
    margin-bottom: 50px;
}

.signup_form > h4{
    margin:15px 0px 10px 0px;
}
.signup_form > select{
    height: 40px;
    font-size: 16px;
    width: 45%;
    border:1px solid #d3d3d3;
    border-radius: 5px;
    background-color: white;
}
.signup_form > input{
    padding:5px;
    width:100%;
    height:30px;
    border-radius: 5px;
    border:1px solid #d3d3d3;
    font-size: 16px;
}
.signup_signup {
    display: inline-block;
    width: 100%;
    height:35px;
    margin-top:30px;
    background-color: rgb(90, 130, 190);
    border:1px solid #dddddd;
    color:white;
    border-radius: 5px;
    font-weight: 600;
    border:1px solid rgb(130, 162, 209);
    padding:8px 0px;
    font-weight: 600;
    outline: none;
}

button a{
    display:inline-block;
    width: 100%;
    height: 100%;
}

.signup_signup a{
    color:white;
}
.idCheck{
    height: 30px;
    border-radius: 5px;
    border:1px solid #e3e3e3;
    background-color: rgb(130, 162, 209);
    color:white;
    font-weight: 600;
    outline: none;
}
@media (max-width: 1058px) {
    .signup_form{
        width: 50%;
    }
    .idCheck{
        right:30%;
    }
}
@media (max-width: 480px) {
    .signup_form{
        width: 100%;
        border:none;
    }
	.signup_form > h2{
        margin:30px 0px;
        font-size: 20px;
    }
    .signup_form input{
        padding:15px 10px;
    }
    .signup_form .sign_up{
        height: 35px;
    }
    .idCheck{
        right:5%;
        height: 45px;
    }
}
.login_wrap{
    margin: 130px 0px 50px 0px;
    min-height: 450px;
    height: 700px;
}
.login_form{
    height:600px;
    margin:0 auto;
    border:1px solid rgb(90, 130, 190);
    border-radius: 25px;
    width: 35%;
    margin: 0 auto;
    padding: 0px 5%;
}

.login_form > h2{
    text-align: center;
    margin-bottom: 50px;
    margin-top:40px;
}

.login_form > h4{
    margin:15px 0px;
}

.login_form > input{
    padding:5px;
    width:100%;
    height:30px;
    border-radius: 5px;
    border:1px solid #d3d3d3;
    font-size: 18px;
}
.sign_button{
    margin-top: 30px;
}
.sign_in{
    display:inline-block;
    width: 100%;
    background-color: rgb(90, 130, 190);
    padding:8px 0px;
    border:1px solid #dddddd;
    border-radius: 5px;
    font-weight: 600;
    outline: none;
}
.sign_up{
    display:inline-block;
    width: 100%;
    background-color: white;
    margin-top:10px;
    padding:8px 0px;
    border:1px solid #dddddd;
    font-weight: 600;
    outline: none;
}
.sign_in > a{
    color:white;
}
.sign_up > a{
    color:black;
}

button a{
    display:inline-block;
    width: 100%;
    height: 100%;
    font-size: 16px;
}
.forget{
    display: inline-block;
    text-align: right;
    font-size: 15px;
    margin-top:10px;
    width:100%;
}

@media (max-width: 1058px) {
    .login_form{
        width: 50%;
    }
}
@media (max-width: 480px) {
    .login_wrap{
        height: 500px;
        margin: 100px 0px 20px 0px;
    }
    .login_form{
        width: 90%;
        height: 400px;
        border:none;
    }
	.login_form > h2{
        margin:30px 0px;
        font-size: 20px;
    }
    .login_form input{
        padding:10px 0px 10px 5px;
    }
    .login_form button{
        height: 35px;
    }
    .sign_up{
        margin-top: 20px;
    }
    .sign_button{
        margin-top: 50px;
    }
    .forget{
        margin-top:30px;
    }
}
